//メディアクエリをmixin化
$breakpoints: (
  'max1124': 'only screen and (max-width: 1124px)', 
  'min1124': 'only screen and (min-width: 1124px)', 
  'sp': 'only screen and (max-width: 768px)', 
  'tb': 'print,screen and (max-width: 991px)', 
  'tc': 'print,screen and (min-width: 992px)', 
  'lb': 'print,screen and (max-width: 1242px)', 
  'lc': 'print,screen and (min-width: 1243px)', 
  'max': 'print,screen and (max-width: 1064px)', 
  'min': 'print,screen and (min-width: 1065px)', 
  'full': 'print,screen and (max-width: 1920px)', 
  'pc': 'print,screen and (min-width: 769px)'
) !default;
@mixin media($breakpoint: md) {
  @media #{map-get($breakpoints, $breakpoint)} {
    @content;
  }
}
