html {
  font-size: 10px;
  @include media('max') {
    font-size: 0.93985vw;
  }
}
body {
  font-family: 'Noto Sans JP', sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  color: #FFFFFF;
  word-break: break-all;
  @include media('sp') {
    font-size: 12px;
    line-height: 20px;
  }
}